<template>
    <v-select class="full-size"
      :placeholder="getValue(element, 'Placeholder')"
      :label="getValue(element, 'Title')"
      v-model="value">
    </v-select>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "./ElementModel.vue";
import { getValue, setValue } from "./store";

export default {
  name: "select-element",
  props: ["element"],
  created() {
    this.$store.commit("addProperties", {
      element: this.element,
      properties: [
        new PropertyModel("Value", undefined),
        new PropertyModel("Title", "Hola"),
        new PropertyModel("Placeholder", "Type something")
      ]
    });
    this.setValue("Width", "auto");
    this.setValue("Height", "auto");
  },
  computed: {
    value: {
      get() {
        return this.getValue(this.element, 'Value');
      },
      set(newValue) {
        this.setValue(this.element, 'Value', newValue)
      }
    }
  },
  methods: {
    getValue,
    setValue
  }
};
</script>

<style scoped>

</style>



